import React from 'react';
import profile from '../src/images/profile.jpg';
// Import the BrowserRouter, Route and Link components
import { BrowserRouter, Route, Link } from 'react-router-dom'; 
import Projects from './Projects.js'; 
import Education from './Education.js'; 
import About from './About.js'; 
import './App.css';
import Experience from './Experience.js';
import Skills from './Skills.js';
import Publications from './Publications.js';
import Interests from './Interests.js';
 
function App() {
  return (
    <BrowserRouter>
 
        <div className="navigation">
        <nav className="navbar navbar-expand-lg navbar-dark bg-primary fixed-top" id="sideNav">
            <a className="navbar-brand js-scroll-trigger" href="#page-top">
                <span className="d-block d-lg-none">Achu Anna Antony</span>
                <span className="d-none d-lg-block"><img className="img-fluid img-profile rounded-circle mx-auto mb-2" src={profile} alt="Profile" /></span>
            </a>
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation"><span class="navbar-toggler-icon"></span></button>
            <div className="collapse navbar-collapse" id="navbarSupportedContent">
                <ul className="navbar-nav">
                  <li className="nav-item"><Link to="/" className="nav-link js-scroll-trigger">About</Link></li>
                  <li className="nav-item"><Link to="/experience" className="nav-link js-scroll-trigger">Experience</Link></li>
                  <li className="nav-item"><Link to="/education" className="nav-link js-scroll-trigger">Education</Link></li>
                  <li className="nav-item"><Link to="/skills" className="nav-link js-scroll-trigger">Skills</Link></li>
                  <li className="nav-item"><Link to="/publications" className="nav-link js-scroll-trigger">Publications</Link></li>
                  <li className="nav-item"><Link to="/interests" className="nav-link js-scroll-trigger">Interests</Link></li>
                  <li className="nav-item"><Link to="/projects" className="nav-link js-scroll-trigger">Projects</Link></li>
                </ul>
            </div>
        </nav>
        </div>
        <div className="App">
        <Route exact path="/" component={About} />
        <Route path="/experience" component={Experience} />
        <Route path="/education" component={Education} /> 
        <Route path="/skills" component={Skills} />
        <Route path="/publications" component={Publications} /> 
        <Route path="/interests" component={Interests} /> 
        <Route path="/projects" component={Projects} />
      </div>
    </BrowserRouter>
  );
}
 
export default App;